import React, { Component } from "react";
import Chart from "chart.js";

import "./SkillsMore.css";
import "./ui.css";

class SkillsMore extends Component {
  constructor(props) {
    super(props);
  }

  componentDidUpdate() {
    let ctx = document.getElementById("chart-area");
    var myChart = new Chart(ctx, {
      type: "pie",
      data: {
        datasets: [
          {
            label: "Usage Precent",
            data: [55, 70, 100, 85, 23, 44],
            backgroundColor: [
              "rgba(255, 99, 132, 1)",
              "rgba(54, 162, 235, 1)",
              "rgba(255, 206, 86, 1)",
              "rgba(75, 192, 192, 1)",
              "rgba(153, 102, 255, 1)",
              "rgba(255, 159, 64, 1)",
            ],
            borderColor: [
              "rgba(255, 99, 132, 1)",
              "rgba(54, 162, 235, 1)",
              "rgba(255, 206, 86, 1)",
              "rgba(75, 192, 192, 1)",
              "rgba(153, 102, 255, 1)",
              "rgba(255, 159, 64, 1)",
            ],
            borderWidth: 1,
          },
        ],
        labels: ["HTML", "CSS", "Javascript", "React", "Redux", "Bootstrap"],
      },
      options: {
        maintainAspectRatio: false,
        responsive: true,
        title: {
          display: true,
          position: "top",
          text: "Usage Percentage",
        },
        legend: {
          position: "bottom",
        },
      },
    });
  }

  render() {
    return (
      <div className={this.props.newClass}>
        <button className='Skills-Button' onClick={this.props.handleClick}>
          More info{" "}
          <i className={this.props.isHidden ? "arrow down" : "arrow up"}></i>
        </button>
        <div id='SkillsMore-canvas-holder'>
          <canvas id='chart-area' width='500' height='500'></canvas>
        </div>
      </div>
    );
  }
}

export default SkillsMore;
